import { Component, OnInit } from '@angular/core';
import { Game } from 'src/app/classes/game';
import { Player } from 'src/app/classes/player';
import { DataTable, Generic, TableTemplate } from 'src/app/classes/table-template';
import { Team } from 'src/app/classes/team';
import { Tip } from 'src/app/classes/tip';
import { DataService } from 'src/app/services/data.service';

const roundNum: number = 4;

@Component({
  selector: 'app-match',
  templateUrl: './match.component.html',
  styleUrls: ['./match.component.css']
})
export class MatchComponent implements OnInit {

  teams!: Team[];
  selectedTeam!: Team;
  games!: Game[];
  gameTemplate: string[] = ["Date", "Home Team", "Score", "Away Team", "Result"];
  gameTemp!: TableTemplate
  venueTemplate: string[] = ["Rival Team", "Venue"]
  venueTemp!: TableTemplate

  constructor(private dataService: DataService) { }

  ngOnInit(): void {

    this.getGame();

    this.getAllTeams();

  }

  onSelect(team: Team): void {

    this.selectedTeam = team;
    let currentYear: number = new Date().getFullYear();
    let tempArr: Game[] = this.games.filter(game => (team && game.year == currentYear
                                                      && (game.hteamid == team.id 
                                                        || game.ateamid === team.id)));
    let round = tempArr.filter(game => game.complete === 100).length + 4;
    console.log(round)

    let dtaTables: DataTable[] = tempArr
    .filter(game => game.round <= round)
    .map((game: Game): DataTable => {

      let dtaTable: DataTable = new DataTable();

      this.gameTemplate.forEach((item: string) => {

        dtaTable.add(this.getGeneric(item, game))

      })

      return dtaTable
    })

    let dtaVenue = tempArr
    .filter(game => game.winnerteamid == this.selectedTeam.id)
    .map((game: Game): DataTable => {

      let dtaTable: DataTable = new DataTable();

      this.venueTemplate.forEach((item: string) => {

        dtaTable.add(this.getGeneric(item, game))

      })

      return dtaTable

    })

    this.gameTemp = new TableTemplate(dtaTables, this.gameTemplate, "20% 25% 20% 25% 10%");
    this.venueTemp = new TableTemplate(dtaVenue, this.venueTemplate, "40% 60%");

  }

  getGeneric(value: string, data: Game): Generic {

    const { date, ateam, hteam, ascore, hscore, venue, hteamid } = data;

    switch (value) {

      case "Rival Team":
        let rivalTeam = hteamid == this.selectedTeam.id ? ateam : hteam
        return new Generic(value, rivalTeam)

      case "Venue":
        return new Generic(value, venue)

      case "Date":
        return new Generic(value, date)

      case "Home Team":
        return new Generic(value, hteam)

      case "Score":
        let score = `${hscore} - ${ascore}`
        return new Generic(value, score)

      case "Away Team":
        return new Generic(value, ateam)
      
      case "Result":
      default:
        return new Generic(value, this.getResult(data))

    }

  }

  getAllTeams(): void {

    this.dataService.getTeam().subscribe(temp => {
       
      this.teams = temp; 

      this.getPlayers();
      
    });

  }

  getPlayers() {

    this.dataService.getPlayers().subscribe((temp: Player[]) => {

      this.teams.forEach((team: Team) => {

        let pav: Player[];

        pav = temp.filter((player: Player) => player.team == team.id);

        team.setPlayer(pav);

      })

    })

  }

  getGame() {
    
    this.dataService.getGame().subscribe(temp => {

      this.games = temp

    })
  
  }

  getTip(game: number = 1, year: string = '2021', round: number = 20): Tip[] {

    let tips: Tip[] = []

    this.dataService.getTip(game, year, round).subscribe(temp => {
      
      temp.forEach((item: any) => {

        tips.push(item)
      
      })

    })

    return tips

  }

  getResult(data: Game) {

    return !data.winnerteamid ? '' : data.winnerteamid == this.selectedTeam.id ? 'W' : 'L';

  }

}
