import { Tip } from './tip';

export class Game {

    hteamid!: number;
    ateamid!: number;
    hteam!: string;
    ateam!: string;
    winner!: string;
    ascore!: number;
    hscore!: number;
    roundname!: string;
    winnerteamid!: number;
    complete!: number;
    year!: number;
    venue!: string
    localtime!: string
    date!: string
    id!: number
    round!: number
    tip!: Tip[]

    constructor(hteamid: number, 
        ateamid: number, 
        hteam: string, 
        ateam:string, 
        winner:string, 
        hscore:number, 
        ascore:number, 
        roundname: string,
        winnerteamid: number,
        complete:number,
        year: number,
        venue: string,
        localtime: string,
        date: string,
        id: number,
        round: number) {
            this.ateamid = ateamid;
            this.hteamid = hteamid;
            this.hteam = hteam;
            this.ateam = ateam;
            this.winner = winner;
            this.hscore = hscore; 
            this.ascore = ascore;
            this.roundname = roundname;
            this.winnerteamid = winnerteamid;
            this.complete = complete;
            this.year = year;
            this.venue = venue;
            this.localtime = localtime;
            this.date = date;
            this.id = id;
            this.round = round;
    }

    setTip(tip: Tip[]) {

        this.tip = tip;

    }
}
