import { Player } from './player';
import { Standing } from './standing';

export class Team {
    logo!:string;
    abbrev!:string;
    name!:string;
    id!:number;
    standing!: Standing
    player!: Player[]

    constructor (name:string, logo:string, id:number, abbrev:string) {
        this.logo = logo;
        this.id = id;
        this.name = name;
        this.abbrev = abbrev;
    }

    setStanding(standing: Standing) {

        this.standing = standing;
    
    }

    setPlayer(player: Player[]) {

        this.player = player

    }
}
