export class TableTemplate {

    data!: DataTable[];
    headerName!: string[];
    template!: string; 

    constructor(
        data: DataTable[],
        headerName: string[],
        template: string) 
        {

            this.data = data;
            this.headerName = headerName;
            this.template = template;

        }


}

export class Generic {

    key!: string;
    value!: any;

    constructor(
        key: string,
        value: any
    )
    {

        this.key = key;
        this.value = value;

    }

}

export class DataTable {

    data: Generic[] = [];

    constructor() {}

    add(item: Generic) {

        this.data.push(item);

    }

}
