export class Tip {

    source!:string;
    hteam!:string;
    ateam!:string;
    tip!: string;
    hconfidence!: string;
    aconfidence!: string;
    margin: string;

    constructor(
        source:string, 
        hteam:string, 
        ateam:string, 
        tip:string,
        hconfidence: string,
        margin: string) {
            this.source = source;
            this.hteam = hteam;
            this.ateam = ateam;
            this.tip = tip;
            this.hconfidence = hconfidence;
            this.aconfidence = (100.00 - parseFloat(hconfidence)).toFixed(2)
            this.margin = margin
    }
}
