import { Component, OnInit } from '@angular/core';
import { Game } from 'src/app/classes/game';
import { Standing } from 'src/app/classes/standing';
import { DataTable, Generic, TableTemplate } from 'src/app/classes/table-template';
import { Team } from 'src/app/classes/team';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-rival-match',
  templateUrl: './rival-match.component.html',
  styleUrls: ['./rival-match.component.css']
})
export class RivalMatchComponent implements OnInit {
  
  favouriteTeam!: Team
  rivalTeam!: Team
  teams!: Team[]
  games!: Game[]
  standings!: Standing[]
  isLoading: boolean = false
  fiveGame!: Game[]
  gameTemplate: string[] = ["Date", "Home Team", "Score", "Away Team", "Result"];
  gameTemp!: TableTemplate
  preGameTemp!: TableTemplate

  constructor(private dataService: DataService) { }

  ngOnInit(): void {

    this.isLoading = true
    this.getAFLTeams()
    this.getGame()
  }

  getAFLTeams(): void {

    this.dataService.getTeam().subscribe(temp => { 
      
      this.teams = temp
      
    });
  
  }

  onSelect(data: Team, type: string): void {

    console.log(data)
    switch (type) {

      case 'favourite':

        this.favouriteTeam = data
        break

      case 'rival':
      default:

        this.rivalTeam = data
        break

    }

    if (this.favouriteTeam && this.rivalTeam)
      this.getFiveGame();

  }

  getFiveGame() {
    
    this.fiveGame = []
    let dtaTables: DataTable[] = []
    let dtaPreTables: DataTable[] = []
    
    let currentYear: number = new Date().getFullYear();

    this.games
    .filter((game) => (game.hteamid == this.favouriteTeam.id && game.ateamid == this.rivalTeam.id) 
            || (game.ateamid == this.favouriteTeam.id && game.hteamid == this.rivalTeam.id))
    .forEach(game => {

      const { complete, year } = game;

      if (complete == 100 && year == currentYear) {
          
        let dtaTable: DataTable = new DataTable();

        this.gameTemplate.forEach((item: string) => {

          dtaTable.add(this.getGeneric(item, game))

        })

        dtaTables = [ ...dtaTables, dtaTable ]

      }
      else if (complete == 100 && year == (currentYear - 1)) {

        let dtaTable: DataTable = new DataTable();

        this.gameTemplate.forEach((item: string) => {

          dtaTable.add(this.getGeneric(item, game))

        })

        dtaPreTables = [ ...dtaPreTables, dtaTable ]

      }

    })

    this.gameTemp = new TableTemplate(dtaTables, this.gameTemplate, "20% 25% 20% 25% 10%")
    this.preGameTemp = new TableTemplate(dtaPreTables, this.gameTemplate, "20% 25% 20% 25% 10%")
    
  }

  gameWons = () => this.fiveGame.filter(x => x.winnerteamid == this.favouriteTeam.id).length

  gameDraw = () => this.fiveGame.filter(x => !x.winnerteamid).length

  gameLosses = () => this.fiveGame.filter(x => x.winnerteamid != this.favouriteTeam.id).length

  getGame() {

    this.dataService.getGame().subscribe((temp: Game[]) => {

      let year = new Date().getFullYear();

      let lastRound = temp.reverse().find(x => x.complete == 100 && x.year == year)?.round ?? 0;

      this.games = temp;
      
      this.getStanding(year.toString(), lastRound)

    })

  }

  getStanding(year: string = '2021', round: number = 20) {

    this.dataService.getStanding(year, round).subscribe((temp: any) => {
      
      temp.forEach((item: Standing) => {

        this.teams.forEach(team => {

          if (team.id == item.id) team.setStanding(item);
          
        })

      })

      this.isLoading = false

    })

  }

  disableFavourite(data: Team): boolean {

    return this.rivalTeam && this.rivalTeam.id == data.id

  }

  disableRival(data: Team): boolean {

    return this.favouriteTeam && this.favouriteTeam.id == data.id

  }

  getGeneric(value: string, data: Game): Generic {

    const { date, ateam, hteam, ascore, hscore, venue, hteamid } = data;

    switch (value) {

      case "Venue":
        return new Generic(value, venue)

      case "Date":
        return new Generic(value, date)

      case "Home Team":
        return new Generic(value, hteam)

      case "Score":
        let score = `${hscore} - ${ascore}`
        return new Generic(value, score)

      case "Away Team":
        return new Generic(value, ateam)
      
      case "Result":
      default:
        return new Generic(value, this.getResult(data))

    }

  }

  getResult(data: Game) {

    return !data.winnerteamid ? '' : data.winnerteamid == this.favouriteTeam.id ? 'W' : 'L';

  }

}
