import { Component, OnInit } from '@angular/core';
import { Game } from 'src/app/classes/game';
import { DataTable, Generic, TableTemplate } from 'src/app/classes/table-template';
import { Team } from 'src/app/classes/team';
import { Tip } from 'src/app/classes/tip';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-prediction',
  templateUrl: './prediction.component.html',
  styleUrls: ['./prediction.component.css']
})
export class PredictionComponent implements OnInit {

  teams!: Team[]
  game!: Game
  games: Game[] = []
  isLoading: boolean = false
  tips: Tip[] = []
  selectedTeam!: Team
  predictionTable!: TableTemplate
  predictionHeader: string[] = [ "Source", "Home Team", "Home Confidence", "Away Confidence", "Away Team" ] 

  constructor(private dataService: DataService) { }

  ngOnInit(): void {
    
    this.getAFLTeams()
    
    this.getGames();
  
  }

  getAFLTeams(): void {

    this.dataService.getTeam().subscribe(temp => { this.teams = temp; });
  
  }

  getTip(game: number = 1, year: string = '2021', round: number = 20) {

    this.dataService.getTip(game, year, round).subscribe((temp: Tip[]) => {

      this.tips = temp

      let dta = temp.map((tip: Tip): DataTable => {
      
        let dtaTable: DataTable = new DataTable();

        this.predictionHeader.forEach((item: string) => {

          dtaTable.add(this.getGeneric(item, tip));

        })

        return dtaTable

      })

      this.predictionTable = new TableTemplate(dta, this.predictionHeader, "20% 20% 20% 20% 20%")

      this.isLoading = false
      
    })

  }

  onSelect(team: Team): void {
    
    this.isLoading = true;
    this.selectedTeam = team;
    this.getPrediction()
    
  }

  getGames() {

    this.dataService.getGame().subscribe(temp => {

      this.games = temp

    })

  }

  getPrediction() {

    let currentYear: number = new Date().getFullYear();

    this.games.forEach((x: Game) => { 
      if(x.complete == 0
        && x.year === currentYear 
        && (x.ateamid == this.selectedTeam.id 
          || x.hteamid == this.selectedTeam.id)) 
        {
          this.game = x;
          return
        }
    
    });

    this.getTip(this.game.id, currentYear.toString(), this.game.round)

  }

  getConfidence(data: Tip) {

    const { hteamid } = this.game
    const { id } = this.selectedTeam

    return hteamid == id ? data.hconfidence : data.aconfidence

  }

  getRivalTeam(): Team {

    const { hteamid, ateamid } = this.game
    const { id } = this.selectedTeam

    let teamid = hteamid === id ? ateamid : hteamid
    let idx = this.teams.findIndex(item => item.id == teamid);

    return this.teams[idx];

  }

  getGeneric(value: string, tip: Tip): Generic {

    const { aconfidence, hconfidence, source, ateam, hteam } = tip

    switch (value) {

      case "Source":
        return new Generic(value, source)

      case "Home Confidence":
        return new Generic(value, `${hconfidence}%`)

      case "Away Confidence":
        return new Generic(value, `${aconfidence}%`)

      case "Home Team":
        return new Generic(value, hteam)

      case "Away Team":
      default:
        return new Generic(value, ateam)

    }

  }

}
